.App {
  text-align: center;
  background:#f2f2f2;
}

footer{
  padding:24px 0px;
  margin-top: 32px;
  color: rgba(0,0,0,0.3);
}
footer b{
  color:rgb(214, 89, 116);
}
.navbar{
  position: fixed;
  top: 0px;
  left:0px;
  right: 0px;
  z-index:+2;
  color: rgba(255,255,255,0.6);
  padding: 20px;
  background: radial-gradient(circle at top right,#11a3bde7,#11a3bdf3);
  border-bottom: 1px solid rgba(255,255,255,0.2); 
}
.navbar b{
  letter-spacing: 1px;
  font-weight: normal;
  padding:4px;
  border:1px solid  rgba(255,255,255,0.6);
  display: inline-block
}

.navbar b.sketchy{
  border-right: none;
  padding-left:12px;
  border-radius: 4px 0px 0px 4px;
}
.navbar b.rhymes{
  border-left: none;
  padding-right:12px;
  border-radius: 0px 4px 4px 0px;
}


.App-logo {
  height: 80vh;
  pointer-events: none;
}

.App-header {
  background-color: #11a2bd;
  background: radial-gradient(circle at top right,#11a2bd,#0e7e92);
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.scrollDown{
  position: absolute;
  bottom:16px;
  letter-spacing: 2px;
  font-size: 12px;
  animation: scrollAnimate infinite 1s linear;
  opacity: 0.4;
}
.blog{
  width: 100%;
  padding:0px;
  box-sizing: border-box;
}

@media(max-width:500px){
  .App-logo {
    height:auto;
    width: 95vw;
  }
}

@keyframes scrollAnimate{
  0%{bottom:16px}
  50%{bottom:24px}
  75%{bottom:16px}
}

.blogPost{
  width: 100%;
  box-sizing: border-box;
  padding: 32px 20px;
  text-align: left;
}

.blogPost img{
  width:100%;
  box-shadow: 0px 2px 10px rgba(0,0,10,0.14);
  border-radius: 8px;
  margin:16px 0px;
  
}
.blogPost b{
  font-size: 24px;
  font-weight: normal;
  margin:0px 4px;
}
.blogPost p{
  margin:0px 8px;
  margin-top:4px;
  color:rgba(0,0,0,0.3);
}
@media(min-width:500px){
  .blog{
    max-width: 800px;
    margin: auto;
  }
  .blogPost{
    width: 50%;
    display: inline-block;
  }
}

.blogPage{
  position: fixed;
  top:0px;
  right:0px;
  left:0px;
  height: 100vh;
  background: #f2f2f2;
  overflow: hidden;
  text-align: left;
  z-index:+3;

}
.blogPage img{
  flex: 0 0 auto;
  height: 100%;
  padding-right:32px;
  box-shadow: 0px 2px 10px rgba(0,0,10,0.14);

}
.blogPage b.titleText{
  font-size: 24px;
  font-weight: normal;
  padding:32px 0 0 32px;
  display: block;
  color:#c2c2c2;
}
.blogPage button.backButton{
  float: right;
  margin: 32px;
  background:transparent;
  border:1px solid rgba(255,255,255,0.4);
  color:  rgba(255,255,255,0.4);
  border-radius: 4px;
  padding: 4px;
}
.postImages{
  padding:32px;
  padding-top: 0px;
  height: 50vh;
  width: 100vw;
  box-sizing: border-box;
  display: flex;
  flex-wrap: nowrap;
  overflow-x: scroll;
  justify-content: safe
}
.blogPage p.caption{
  margin: 32px;
  color:rgba(0,0,0,0.3);

}
.postTopSection{
  background: radial-gradient(circle at top right,#04080f,rgb(3, 15, 26));
}
.textContent b.titleText{
  color: #0e7e92;
}
@media(min-width:500px){
  .postTopSection{
    width: 50%;
    height: 100%;
    float: left;
  }
  .postImages{
    width:100%;
    height: 100%;
    margin:0 auto;
    text-align: center;
    display: block;
    padding-top:80px;
  }
  .postImages img{
    width: 70%;
    height: auto;
    margin: 32px;
  }
  .textContent{
    width: 50%;
    height: 100vh;
    display: inline-block;
    overflow-y:auto;
  }
  .textContent button.backButton{
    border:1px solid rgba(0, 3, 3, 0.4);
    color:  rgba(0,3,3,0.4);
  }
}




@media(min-width:500px){
  .mob{
    display: none !important;
  }
}
@media(max-width:500px){
  .nonmob{
    display: none !important;
  }
}